import 'scss/global.scss'

import React from 'react'
import ReactDOM from 'react-dom'

import App from './App'
import AppUnauthorized from './AppUnauthorized'

ReactDOM.render(
	<React.StrictMode>
		{localStorage.getItem('token') ? <App /> : <AppUnauthorized />}
	</React.StrictMode>,
	document.getElementById('root')
)

